<script>
/* eslint-disable no-unused-vars */
const parser = require('any-date-parser');
// import { AddToCalendar } from 'vue-add-events-to-google-calendar';
// import moment from "moment";
import * as htmlToImage from 'html-to-image';
import {mapState} from "vuex";
var L = require('leaflet');
var download = require("downloadjs");
export default {
  name: 'MeetsPage',
  components: {
    // AddToCalendar
  },
  watch: {
    meetsLoadingStatus: {
      handler() {
        if (this.meetsLoadingStatus) {
         // document.webkitCancelFullScreen();
          this.$nextTick(() => {
            window.scrollTo(0, 0);
            new Promise((res) => {
              try {
                this.info = this.$meets.filter(i => `/${i.url}` === window.location.pathname)[0];

                res(this.info);
                if (this.info.Instagram) {
                  // this.instagrams = this.info.Instagram.split("\n");
                  this.instagrams = this.info.Instagram;
                }
              } catch {
                // window.location.href = "/";
              }
            }).then(() =>{
              // this.renderMap();
              this.$nextTick(() => {
                // this.resizeImage(this.info.Image[0]);
                window.addEventListener("resize", () => this.resizeImage(this.info.Image[0]));
              })
            });
          })
        }
      },
      immediate:true
    }
  },
  computed: {
    ...mapState([ "lang", "meetsLoadingStatus"])
    
  },
  methods: {
    parsedInfo(string) {
      var parsed = string.replace(/(?:\r\n|\r|\n)/g, '<br />');
      return parsed;
    },
    renderMap() {
      return new Promise((res) => {
        
        this.map = L.map('map', {
            center: [this.info.Lat , this.info.Long],
            zoom: 12
        });
        L.tileLayer('https://a.tile.openstreetmap.fr/hot/{z}/{x}/{y}.png', {attribution: '<a href="https://carto.com/" target="_blank">&copy; CARTO</a> <a href="https://www.maptiler.com/copyright/" target="_blank">&copy; MapTiler</a> <a href="https://www.openstreetmap.org/copyright" target="_blank">&copy; OpenStreetMap contributors</a>'}).addTo(this.map);
        
        var meetsIconToday = L.icon({
          iconUrl: require('../assets/C_Map_Marker_Car_Meets_TODAY.png'),
          iconSize: [30, 42],
          iconAnchor: [15, 42],
      });

      L.marker([this.info.Lat, this.info.Long], {icon: meetsIconToday}).addTo(this.map);
        
        res(this.map);
      })
      
    },
    isolate(opt) {
      if (opt === `prompt`) {
        this.isolated = true;
      } else {
        this.isolated = false;
      }
      if (this.isolated) {
        document.getElementById("banner").style = `
          position: fixed;
          top: 0;
          width: 100%;
          z-index: 100;
          height: 100%;
          margin-bottom: 100px;
          cursor: pointer;
          padding-top: 0px;
      `;
      document.getElementById("info").style = `
          padding-bottom: 20px;
      `
      } else {
         document.getElementById("banner").style = ``;
         document.getElementById("info").style = `
          padding-bottom: 20px;
      `
      }
  
    },
    
    processTime() {
      let engMonth;
      if (this.$store.state.lang === "fr") {
        switch(this.info.Month) {
          case "Janvier": {
            engMonth = "January";
            break;
          }
          case "Fevrier": {
            engMonth = "February";
            break;
          }
          case "Mars": {
            engMonth = "March";
            break;
          }
          case "Avril": {
            engMonth = "April";
            break;
          }
          case "Mai": {
            engMonth = "May";
            break;
          }
          case "Juin": {
            engMonth = "June";
            break;
          }
          case "Juillet": {
            engMonth = "July";
            break;
          }
          case "Aout": {
            engMonth = "August";
            break;
          }
          case "Septembre": {
            engMonth = "September";
            break;
          }
          case "Octobre": {
            engMonth = "October";
            break;
          }
          case "Novembre": {
            engMonth = "November";
            break;
          }
          case "Decembre": {
            engMonth = "December";
            break;
          }
        }
      } else {
        engMonth = this.info.Month;
      }
      
      // console.log(engMonth);
      let start = this.parser.attempt(`${engMonth} ${this.info.Day}, ${this.info.Year}`);
      let starttime, startmonth, startday, endtime ,endmonth,endday;
      // console.log(start);

      startmonth = start.month.toLocaleString(this.locale, { minimumIntegerDigits: 2, useGrouping: false});
      startday = start.day.toLocaleString(this.locale, { minimumIntegerDigits: 2, useGrouping: false});

      endmonth = startmonth;
      endday = startday;

      if (this.info['Time']) {
        starttime = this.parser.attempt(this.info['Time']);
        starttime = {
          hour: starttime.hour.toLocaleString(this.locale, { minimumIntegerDigits: 2, useGrouping: false}),
          minute: starttime.minute ? starttime.minute.toLocaleString(this.locale, { minimumIntegerDigits: 2, useGrouping: false}) : "00"
        }
        starttime = starttime.hour + `${starttime.minute}00`;
         // default det end time to start time if no end time is found
      }
 
      // process end time if there is any
      if (this.info['End Time']) {
        endtime = this.parser.attempt(this.info['End Time']);
        endtime = {
          hour: endtime.hour.toLocaleString(this.locale, { minimumIntegerDigits: 2, useGrouping: false}),
          minute: endtime.minute ? endtime.minute.toLocaleString(this.locale, { minimumIntegerDigits: 2, useGrouping: false}) : "00"
        }
        endtime = endtime.hour + `${endtime.minute}00`;
      } else if (!this.info['End Time'] && this.info['Time']) {
        endtime = starttime;
      }

      if (this.info['Date_Until']) {
        endmonth = this.parser.attempt(this.info['Date_Until']).month.toLocaleString(this.locale, { minimumIntegerDigits: 2, useGrouping: false});
        endday = (this.parser.attempt(this.info['Date_Until']).day + 1).toLocaleString(this.locale, { minimumIntegerDigits: 2, useGrouping: false});
      }
  
      let parsedDate = `${start.year}${startmonth}${startday}${starttime ? `T`+starttime+`EST` : ``}%2F${start.year}${startmonth}${startday}${endtime ? `T`+endtime+`EST` : ``}`;
      return parsedDate;
    },
    resizeImage(img) {
      const defaults = `position: absolute;
            display: block;
            left: 50%;
            top: 50%;
            transform: translateY(-50%)  translateX(-50%);`;

            // bannerimagewrapper height
            //image height
            var calculatedWidth = window.innerWidth / this.info.Image[0].width * this.info.Image[0].height;
            var bannerWrapperHeight = document.getElementById("bannerimagewrapper").getBoundingClientRect().height;
            // console.log(bannerWrapperHeight, calculatedWidth);

      if (img.width > img.height || calculatedWidth < bannerWrapperHeight) {
        document.getElementById("bannerimage").style = `
          height: 100%;
          ${defaults}
          `;
      } else {
        // this.resizeBannerImageWrapper();
        document.getElementById("bannerimage").style =  `
          width: 100%;
          ${defaults}
        `;
      }
    },

    instagramsLink(item) {
      if (item.includes("@")) {
        return `https://www.instagram.com/${item.split("@")[1]}`;
      }
      else if (item.includes("#")) {
        return `https://www.instagram.com/explore/tags/${item.split("#")[1]}`;
      }
    },

    screenGrab() {
      // console.log(this.info.id);
      var div = document.getElementById('banner');
      htmlToImage.toPng(div)
        .then((dataUrl) => {
          // console.log(dataUrl);
        // download(dataUrl);
        }, err => {
          console.warn(err);
        });
    }
  },

  data() {
    return {
      info:null,
      map: null,
      dataUrl: ``,
      isolated: false,
      instagrams: [],
      parser: parser,
      locale: "en-US"
    }
  },

 mounted() {
   
}
  
}
</script>

